/* global $ */
import React from "react";
import "./Projects.css";
import p1img from "../../assets/Project/hiccup1.jpg";
import p2img from "../../assets/Project/cowboy1.jpg";
import p3img from "../../assets/Project/hiccup2.jpg";
import pathfindimg from "../../assets/Project/pathfinding-visualizer.JPG"
import portfolioimg from "../../assets/Project/portfolio.JPG";

export default function Projects() {
  $(".btn").mouseup(function () {
    $(this).blur();
  });

  return (
    <div className="banner-image vh-100 d-flex justify-content-center align-items-center">
      <div id="scrollable-div">
        <div className="nav-space"></div>
        <div className="container">
          <div className="row g-4">
            <div className="col-12 col-md-6 col-lg-4">
              <div className="card">
                <img src={pathfindimg} alt="null" className="card-img-top"></img>
                <div className="card-body">
                  <h5 className="card-title">
                    Dijkstra's Algorithm Visualization Tool
                  </h5>
                  <p className="card-text">
                    This project utilizes Dijkstra's Algorithm and a min heap
                    data structure to find the shortest path between two nodes.
                  </p>
                  <div className="card-button-container">
                    <a
                      href="https://masonwatson.github.io/pathfinding-visualizer/"
                      target="_blank"
                      class="btn btn-primary card-button"
                    >
                      Visit the Application
                    </a>
                    <a
                      href="https://github.com/masonwatson/pathfinding-visualizer/tree/master"
                      target="_blank"
                      class="btn btn-primary card-button"
                    >
                      View the Repository
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12 col-md-6 col-lg-4">
              <div className="card">
                <img src={p2img} alt="null" className="card-img-top"></img>
                <div className="card-body">
                  <h5 className="card-title">
                    Sorting Algorithm Visualization Tool
                  </h5>
                  <p className="card-text">
                    This project utilizes various sorting algorithms, such as
                    merge and bubble sort, to sort an array of random data from
                    smallest to largest.
                  </p>
                  <div className="card-button-container">
                    <a href="#" class="btn btn-primary card-button">
                      Visit the Application
                    </a>
                    <a href="#" class="btn btn-primary card-button">
                      View the Repository
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12 col-md-6 col-lg-4">
              <div className="card">
                <img src={p3img} alt="null" className="card-img-top"></img>
                <div className="card-body">
                  <h5 className="card-title">
                    Full Stack Real Estate Application
                  </h5>
                  <p className="card-text">
                    This project is a full stack real estate application that
                    emulates modern day websites such as Realtor.com or Zillow.{" "}
                  </p>
                  <div className="card-button-container">
                    <a href="#" class="btn btn-primary card-button">
                      Visit the Application
                    </a>
                    <a href="#" class="btn btn-primary card-button">
                      View the Repository
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12 col-md-6 col-lg-4">
              <div className="card">
                <img src={p3img} alt="null" className="card-img-top"></img>
                <div className="card-body">
                  <h5 className="card-title">Notepad Application</h5>
                  <p className="card-text">
                    A notepad appliction developed with HTML, CSS, JavaScript
                    and React.
                  </p>
                  <div className="card-button-container">
                    <a href="#" class="btn btn-primary card-button">
                      Visit the Application
                    </a>
                    <a href="#" class="btn btn-primary card-button">
                      View the Repository
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12 col-md-6 col-lg-4">
              <div className="card">
                <img src={p3img} alt="null" className="card-img-top"></img>
                <div className="card-body">
                  <h5 className="card-title">Checkers PyGame</h5>
                  <p className="card-text">
                    This project uses Python and the PyGame library to implement
                    the classic game of checkers.
                  </p>
                  <div className="card-button-container">
                    <a href="#" class="btn btn-primary card-button">
                      Watch the Demo
                    </a>
                    <a href="#" class="btn btn-primary card-button">
                      View the Repository
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12 col-md-6 col-lg-4">
              <div className="card">
                <img src={p3img} alt="null" className="card-img-top"></img>
                <div className="card-body">
                  <h5 className="card-title">
                    Matching Dice Command Line Game
                  </h5>
                  <p className="card-text">
                    An automated matching dice game written in C. This project
                    uses POSIX threads to determine player order. The winning
                    team is determined when both players on a team roll the same
                    total.
                  </p>
                  <div className="card-button-container">
                    <a href="#" class="btn btn-primary card-button">
                      Watch the Demo
                    </a>
                    <a href="#" class="btn btn-primary card-button">
                      View the Repository
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12 col-md-6 col-lg-4">
              <div className="card">
                <img
                  src={portfolioimg}
                  alt="null"
                  className="card-img-top"
                ></img>
                <div className="card-body">
                  <h5 className="card-title">My Portfolio Website</h5>
                  <p className="card-text">
                    Inception. My website within my website.
                  </p>
                  <div className="card-button-container">
                    <a
                      href="https://masonxwatson.com/"
                      role="button"
                      class="btn btn-primary card-button"
                    >
                      Home Page
                    </a>
                    <a
                      href="https://github.com/masonwatson/portfolio/tree/master"
                      target="_blank"
                      class="btn btn-primary card-button"
                    >
                      View the Repository
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
