import React, { useEffect, useState } from "react";
import "./Home.css";
import useTimer from "./homeHooks/useTimer";
import useHomeText from "./homeHooks/useHomeText";
import Resume from "../../assets/Resume/resume.txt";

const text_attributes = ["Mason Watson.", "Software Engineer."];

export default function Home() {

  // Home Text Opacity - change opacity on scroll
  const [homeTextOpacity, setHomeTextOpacity] = useState(true);

  // Custom Hooks
  const typedHomeText = useHomeText(text_attributes);
  const currentTime = useTimer();

  function mount_home() {

    const changeHomeTextOpacity = () => {
      if (window.scrollY > 50) {
        setHomeTextOpacity(false);
      } else if (window.scrollY <= 50) {
        setHomeTextOpacity(true);
      }
    };

    window.addEventListener("scroll", () => {
      changeHomeTextOpacity();
    });
  }

  useEffect(() => {
    let isMounted = true;

    if (isMounted) mount_home();

    return(() => {
      isMounted = false;
    });
  }, []);

  return (
    <div id="home" className="home-about-container">
      <div className="home-section">
        <div className="banner-image vh-100 d-flex justify-content-center align-items-center">
          <div className="home-content text-center">
            <div
              className={
                homeTextOpacity
                  ? "home-brand home-brand-active"
                  : "home-brand home-brand-inactive"
              }
            >
              <span className="home-text blinking-cursor">{typedHomeText}</span>
            </div>
          </div>
        </div>
      </div>
      <div id="about" className="about-section">
        <div className="d-flex justify-content-center align-items-center">
          <div className="about-container container-md">
            <div className="row justify-content-start">
              <div className="col-5">
                <div
                  className="about-me"
                >
                  <h2 className="about-title">About Me.</h2>
                  <h4 className="about-text">
                    I am a forward thinking engineer who works effectively in
                    dynamic team environments as well as a self directed
                    setting. I am a quick learner that can pick up tech stacks
                    and adapt to the exponential growth of the tech industry.
                  </h4>
                  <br />
                  <br />
                </div>
              </div>
            </div>
            <div className="row justify-content-start">
              <div className="col-5">
                <div
                  className="about-education"
                >
                  <h2 className="about-title">
                    I am dedicated to lifelong learning.
                  </h2>
                  <h4 className="about-text">{currentTime}</h4>
                  <br />
                  <br />
                </div>
              </div>
            </div>
            <div className="row justify-content-start">
              <div className="col-5">
                <div
                  className="about-resume"
                >
                  <h2 className="about-title">
                    <a href={Resume} target="_blank" className="resume-button">
                      Click here to download my resume.
                    </a>
                  </h2>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
