/* global $ */
import React, { useEffect, useState } from "react";
import "./Header.css";
import { Link } from "react-router-dom";
import { HashLink } from "react-router-hash-link";

export default function Header() {

  const heightThreshold = 0;
  const [homeOrAboutActive, setHomeOrAboutActive] = useState(true);
  const [homeActive, setHomeActive] = useState(true);
  const [aboutActive, setAboutActive] = useState(false);
  const [timelineActive, setTimelineActive] = useState(false);
  const [contactActive, setContactActive] = useState(false);
  const [projectsActive, setProjectsActive] = useState(false);

  function toTop() {
    window.scrollTo(0, 0);
  }

  function ha_sscroll() {
    const sscrollHome = (currentHeight) => {
      if (currentHeight > heightThreshold && currentHeight <= window.innerHeight) {
        setHomeActive(true);
      } else {
        setHomeActive(false);
      }
    };
    const sscrollAbout = (currentHeight) => {
      if (currentHeight > heightThreshold && currentHeight <= window.innerHeight) {
        setAboutActive(true);
      } else {
        setAboutActive(false);
      }
    };

    window.addEventListener("scroll", () => {
      sscrollHome(((window.innerHeight - window.scrollY) - (window.innerHeight * .45)));
      sscrollAbout(((window.innerHeight * 2 - window.scrollY) - (window.innerHeight * .45)));
    });
  }

  useEffect(() => {
    if(document.getElementById("home") && document.getElementById("about")) {
      ha_sscroll();
    }
  }, [homeOrAboutActive]);

  return (
    <nav
      id="navbar"
      className={`navbar fixed-top bg-dark navbar-expand-lg navbar-dark p-md-3`}
    >
      <div className="container-fluid">
        <a className="navbar-brand">Mason Watson</a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#toggleMobileMenu"
          aria-controls="toggleMobileMenu"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <i class="bi bi-list"></i>
        </button>
        <div className="collapse navbar-collapse" id="toggleMobileMenu">
          <ul className="navbar-nav text-center me-auto mb-2 mb-lg-0">
            <HashLink
              className="hlink"
              spy={true}
              smooth={true}
              offset={0}
              duration={500}
              onClick={() => {
                setHomeOrAboutActive(true);
                setHomeActive(true);
                setAboutActive(false);
                setTimelineActive(false);
                setContactActive(false);
                setProjectsActive(false);
              }}
              to="/#home"
            >
              <li
                className={`nav-item nav-link ${
                  homeActive ? "header-nav-active" : "header-nav-inactive"
                }`}
              >
                Home
              </li>
            </HashLink>
            <HashLink
              className="hlink"
              spy={true}
              smooth={true}
              offset={0}
              duration={500}
              onClick={() => {
                toTop();
                setHomeOrAboutActive(true);
                setHomeActive(false);
                setAboutActive(true);
                setTimelineActive(false);
                setContactActive(false);
                setProjectsActive(false);
              }}
              to="/#about"
            >
              <li
                className={`nav-item nav-link ${
                  aboutActive ? "header-nav-active" : "header-nav-inactive"
                }`}
              >
                About
              </li>
            </HashLink>
            <Link className="link" to="/timeline">
              <li className="nav-item">
                <a
                  className={`nav-link ${
                    timelineActive ? "header-nav-active" : "header-nav-inactive"
                  }`}
                  aria-current="page"
                  href="#"
                  onClick={() => {
                    toTop();
                    setHomeOrAboutActive(false);
                    setHomeActive(false);
                    setAboutActive(false);
                    setTimelineActive(true);
                    setContactActive(false);
                    setProjectsActive(false);
                  }}
                >
                  Timeline
                </a>
              </li>
            </Link>
            <Link className="link" to="/contact">
              <li className="nav-item">
                <a
                  className={`nav-link ${
                    contactActive ? "header-nav-active" : "header-nav-inactive"
                  }`}
                  aria-current="page"
                  href="#"
                  onClick={() => {
                    setHomeOrAboutActive(false);
                    setHomeActive(false);
                    setAboutActive(false);
                    setTimelineActive(false);
                    setContactActive(true);
                    setProjectsActive(false);
                  }}
                >
                  Contact
                </a>
              </li>
            </Link>
            <Link className="link" to="/projects">
              <li className="nav-item">
                <a
                  className={`nav-link ${
                    projectsActive ? "header-nav-active" : "header-nav-inactive"
                  }`}
                  aria-current="page"
                  href="#"
                  onClick={() => {
                    setHomeOrAboutActive(false);
                    setHomeActive(false);
                    setAboutActive(false);
                    setTimelineActive(false);
                    setContactActive(false);
                    setProjectsActive(true);
                  }}
                >
                  My Projects
                </a>
              </li>
            </Link>
          </ul>
          <ul className="navbar-nav navbar-icons ms-auto">
            <li className="nav-item social-icon">
              <a
                className="nav-link"
                aria-current="page"
                href="https://www.linkedin.com/in/masonxw/"
                target="_blank"
                rel="noreferrer"
              >
                <i className="bi bi-linkedin"></i>
              </a>
            </li>
            <li className="nav-item social-icon">
              <a
                className="nav-link"
                aria-current="page"
                href="https://www.instagram.com/masonwatson.py/"
                target="_blank"
                rel="noreferrer"
              >
                <i className="bi bi-instagram"></i>
              </a>
            </li>
            <li className="nav-item social-icon">
              <a
                className="nav-link"
                aria-current="page"
                href="https://twitter.com/mxwcode"
                target="_blank"
                rel="noreferrer"
              >
                <i className="bi bi-twitter"></i>
              </a>
            </li>
            <li className="nav-item social-icon">
              <a
                className="nav-link"
                aria-current="page"
                href="https://github.com/masonwatson"
                target="_blank"
                rel="noreferrer"
              >
                <i className="bi bi-github"></i>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}
